import navigation from '../../components/navigation/navigation';
import uniSwiperDot from '@/components/uni-swiper-dot/uni-swiper-dot.vue';
import uniLoadMore from '../../components/uni-load-more/uni-load-more.vue';
import uniPop from '@/components/uni-popup/uni-pop.vue';
import uniPopup from '@/components/uni-popup/uni-popup.vue';
export default {
	data() {
		return {
			config: {
				type: 2, //0无title，1有title，2类别title
				renttype: 0,
				back: true,
			},
			userId: '',
			owner: {},
			card: {},
			persons: [],
			applyteams: [],
			myTeam: {},
			otherTeams: [],
			comments: [],
			replycomment:{},
			replytocomment:{},
			replylist:[],
			commenttype:0,
			loadingType: 0, //定义加载方式 0---contentdown  1---contentrefresh 2---contentnomore
			contentText: {
				contentdown: '上拉显示更多',
				contentrefresh: '正在加载...',
				contentnomore: '没有更多数据了'
			},
			visible:false,
			visible1:false,
			value:'',
			title:['评论','回复'],
			scrolltotitle: false,
			scrollTop: 0,
			current: 0,
			mode: 'round',
			dotsStyles: {},
			commentpage: 1,
			complete:false,
		}
	},
	onPageScroll(e) {
		this.scrollTop = e.scrollTop;
	},
	computed: {
		height() {
			const {
				platform,
				statusBarHeight
			} = uni.getSystemInfoSync()
			let height = statusBarHeight + 4 //ios 24px
			if (platform.toLowerCase() == "android") {
				height += 4 //android 28px
			}
			return height + 38 + "px"
		},
	},
	watch: {
		scrollTop(newVal, oldVal) {
			if (newVal > 240) {
				this.scrolltotitle = true;
			} else {
				this.scrolltotitle = false;
			}
		}
	},
	filters: {
		filters1: function(arg) {
			if (arg == 1) {
				return '出租';
			} else if (arg == 2) {
				return '出售';
			} else if (arg == 3) {
				return '求租';
			} else if (arg == 4) {
				return '求购';
			} else if (arg == 5) {
				return '找室友';
			}
		},
		filters2: function(arg) {
			if (arg == 0) {
				return '一室一厅';
			} else if (arg == 1) {
				return '两室一厅';
			} else if (arg == 2) {
				return '三室一厅';
			} else if (arg == 3) {
				return '三室两厅';
			} else if (arg == 4) {
				return '其他';
			}
		},
		filters3: function(arg) {
			if (arg == 1) {
				return '整租';
			} else if (arg == 2) {
				return '购房';
			} else if (arg == 3) {
				return '出租';
			} else if (arg == 4) {
				return '出售';
			} else if (arg == 5) {
				return '申请';
			}
		},
	},
	components: {
		navigation,
		uniSwiperDot,
		uniLoadMore,
		uniPopup,
		uniPop
	},
	onLoad(options) {
		if (uni.getStorageSync('userId')) {
			this.userId = uni.getStorageSync('userId')
			this.getmobile()
		} else {
			setTimeout(() => {
				uni.showToast({
					title: '请先授权登录',
					icon: 'none'
				})
			}, 100);
			uni.switchTab({
				url: '../my/my'
			})
		}
		this.config.renttype = options.type;
		this.cardId = options.Id;
		uni.showLoading({
			title: '加载中',
			mask: true
		});
		this.getdetail()
	},
	onShow() {
		this.getdetail()
	},
	onPullDownRefresh: function() {
		//下拉刷新的时候请求一次数据
		this.getdetail();
	},
	onReachBottom: function() {
		this.getmorecomments();
	},
	methods: {
		customConduct() {
			uni.navigateBack({});
		},
		gotoother(e){
			if (this.userId!=e){
				uni.navigateTo({
					url:'../my/other?userId='+e
				})
			}
		},
		change(e) {
			this.current = e.detail.current
		},
		previewImgs(e) {
			puImage(e, this);
		},
		openreply(e){
			this.$refs.popup.open();
			this.replytocomment = e
			this.getreply()
		},
		getreply(){
			let _this = this
			this.$http.get('/api/comment/getReplies/', {
				commentId: this.replytocomment.commentId
			}).then(res => {
				console.log(res)
				_this.replylist = res.data.replies
				
			}).catch(err => {
				console.log(err)
			})
		},
		closereply(){
			this.$refs.popup.close();
		},
		gotolocation(){
			uni.navigateTo({
				url:'../order/mapdetail?card='+this.cardId
			})
		},
		getdetail() {
			let _this = this
			this.$http.get('/api/detail/getCardDetail/', {
				cardId: this.cardId
			}).then(res => {
				console.log(res)
				_this.card = res.data
				_this.getowner()
				_this.getrentlist()
				if (_this.userId == _this.card.ownerId) {
					_this.getapply()
				}
				_this.getcomment()
				uni.hideLoading();
				_this.complete = true;
			}).catch(err => {
				console.log(err)
				uni.hideLoading();
				_this.complete = true;
			})
		},
		getowner() {
			let _this = this
			this.$http.get('/api/person/info/', {
				userId: this.card.ownerId
			}).then(res => {
				console.log(res)
				_this.owner.name = res.name
				_this.owner.avatarUrl = res.avatarUrl
			}).catch(err => {
				console.log(err)
			})
		},
		getapply() {
			let _this = this
			this.$http.get('/api/detail/owner/getApply/', {
				cardId: this.cardId
			}).then(res => {
				console.log(res)
				_this.persons = res.data.person
				_this.applyteams = res.data.teams
			}).catch(err => {
				console.log(err)
			})
		},
		getrentlist() {
			let _this = this
			this.$http.get('/api/union/getTeam/', {
				cardId: this.cardId
			}).then(res => {
				console.log(res)
				_this.myTeam = res.data.myTeam
				_this.otherTeams = res.data.otherTeams
			}).catch(err => {
				console.log(err)
			})
		},
		getcomment() {
			let _this = this
			this.commentpage = 1
			this.loadingType = 2;
			uni.showNavigationBarLoading();
			this.$http.get('/api/comment/getComments/', {
				cardId: this.cardId,
				pageNum: this.commentpage
			}).then(res => {
				console.log(res)
				_this.commentpage = 2
				_this.comments = res.data.comments
				if (res.data.comments.length < 8) {
					console.log('评论区1');
					this.loadingType = 2;
				} else {
					this.loadingType = 0;
				}
				uni.hideNavigationBarLoading(); //关闭加载动画
			}).catch(err => {
				console.log(err)
			})
		},
		getmorecomments(){
			if (this.loadingType !== 0) {
				//loadingType!=0;直接返回
				return false;
			}
			let _this = this
			this.loadingType = 1;
			uni.showNavigationBarLoading();
			this.$http.get('/api/comment/getComments/', {
				cardId: this.cardId,
				pageNum: this.commentpage
			}).then(res => {
				console.log(res)
				if (res.data.comments == null) {
					this.loadingType = 2;
					uni.hideNavigationBarLoading(); //关闭加载动画
					return;
				}
				else if (res.data.comments.length < 8) {
					console.log('评论区');
					this.loadingType = 2;
				} else {
					this.loadingType = 0;
				}
				_this.commentpage++
				_this.comments = _this.comments.concat(res.data.comments)
				uni.hideNavigationBarLoading(); //关闭加载动画
			}).catch(err => {
				console.log(err)
			})
		},
		oneapply() {
			let _this = this
			uni.showModal({
				title: '提示',
				content: '是否确认提出申请',
				success: function(res) {
					if (res.confirm) {
						console.log('用户点击确定');
						_this.$http.post('/api/detail/orderApply/', {
							userId: _this.userId,
							cardId: _this.card.cardId
						}).then(res => {
							console.log(res)
							uni.showToast({
								title: '申请成功',
								icon: 'success'
							})
							_this.getdetail()
						}).catch(err => {
							console.log(err)
						})
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		manyapply() {
			let _this = this
			uni.showModal({
				title: '提示',
				content: '是否确认提出合租申请',
				success: function(res) {
					if (res.confirm) {
						console.log('用户点击确定');
						_this.$http.post('/api/detail/orderTeamApply/', {
							teamId: _this.myTeam.teamId,
							cardId: _this.card.cardId
						}).then(res => {
							console.log(res)
							uni.showToast({
								title: '申请成功',
								icon: 'success'
							})
							_this.getdetail()
						}).catch(err => {
							console.log(err)
						})
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		deleterent(){
			let _this = this
			uni.showModal({
			    title: '提示',
			    content: '是否确定删除帖子',
			    success: function (res) {
			        if (res.confirm) {
			            console.log('用户点击确定');
						_this.$http.post('/api/adm/deleteCard/', {
							cardIds:[_this.cardId]
						}).then(res => {
							console.log(res)
							setTimeout(() => {
								uni.showToast({
									title: '删除成功',
									icon: 'success'
								});
							}, 100);
							uni.switchTab({
								url:'../home/home'
							})
						}).catch(err => {
							console.log(err)
						})
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		},
		processapply(e){
			let _this = this
			uni.showModal({
				title: '提示',
				content: '是否确认同意该申请',
				success: function(res) {
					if (res.confirm) {
						console.log('用户点击确定');
						_this.$http.post('/api/detail/processApply/', {
							applyId:e
						}).then(res => {
							console.log(res)
							setTimeout(() => {
								uni.showToast({
									title: '操作成功',
									icon: 'success'
								});
							}, 100);
							uni.switchTab({
								url:'../home/home'
							})
						}).catch(err => {
							console.log(err)
						})
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		createteam() {
			let _this = this
			uni.showModal({
				title: '提示',
				content: '是否确认创建合租队列',
				success: function(res) {
					if (res.confirm) {
						console.log('用户点击确定');
						_this.$http.post('/api/union/createTeam/', {
							userId: _this.userId,
							cardId: _this.card.cardId,
							maxNum: _this.card.unionNum
						}).then(res => {
							console.log(res)
							uni.showToast({
								title: '新建成功',
								icon: 'success'
							})
							_this.getrentlist()
						}).catch(err => {
							console.log(err)
						})
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		jointeam(e){
			let _this = this
			uni.showModal({
				title: '提示',
				content: '是否确认加入合租队列',
				success: function(res) {
					if (res.confirm) {
						console.log('用户点击确定');
						_this.$http.post('/api/union/joinTeam/', {
							userId: _this.userId,
							teamId: e,
						}).then(res => {
							console.log(res)
							uni.showToast({
								title: '加入成功',
								icon: 'success'
							})
							_this.getrentlist()
						}).catch(err => {
							console.log(err)
						})
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		leaveteam(e){
			let _this = this
			let content = e==0?'是否确认解散合租队列':'是否确认退出合租队列'
			let title = e==0?'解散成功':'退出成功'
			uni.showModal({
				title: '提示',
				content: content,
				success: function(res) {
					if (res.confirm) {
						console.log('用户点击确定');
						_this.$http.post('/api/union/leaveTeam/', {
							userId: _this.userId,
							teamId: _this.myTeam.teamId,
						}).then(res => {
							console.log(res)
							uni.showToast({
								title: title,
								icon: 'success'
							})
							_this.getrentlist()
						}).catch(err => {
							console.log(err)
						})
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		comment(){
			this.visible = true;
			this.value = ''
			this.commenttype = 0
		},
		reply(e){
			this.visible1 = true;
			this.value = ''
			this.commenttype = 1
			this.replycomment = e
			console.log(e)
		},
		close(){
			this.visible = false;
			this.visible1 = false;
		},
		handlecomment(){
			let _this = this
			if (this.commenttype == 0){
				this.$http.post('/api/comment/addComment/', {
					userId: this.userId,
					cardId: this.card.cardId,
					content: this.value,
					replyToId: null
				}).then(res => {
					console.log(res)
					uni.showToast({
						title: '评论成功',
						icon: 'success'
					})
					_this.visible = false;
					_this.getcomment()
				}).catch(err => {
					console.log(err)
				})
			} else if (this.commenttype == 1){
				this.$http.post('/api/comment/addComment/', {
					userId: this.userId,
					cardId: this.card.cardId,
					content: this.value,
					replyToId: (this.replycomment.commentId?this.replycomment.commentId:this.replycomment.replyId)
				}).then(res => {
					console.log(res)
					uni.showToast({
						title: '回复成功',
						icon: 'success'
					})
					_this.visible1 = false;
					_this.getreply()
					_this.getcomment()
				}).catch(err => {
					console.log(err)
				})
			}
		},
		deletecomment(e){
			let _this = this
			uni.showModal({
				title: '提示',
				content: '是否删除评论',
				success: function(res) {
					if (res.confirm) {
						console.log('用户点击确定');
						_this.$http.post('/api/comment/deleteComment/', {
							commentId: e,
						}).then(res => {
							console.log(res)
							uni.showToast({
								title: '删除成功',
								icon: 'success'
							})
							_this.visible = false;
							_this.visible1 = false;
							_this.closereply();
							_this.getcomment()
						}).catch(err => {
							console.log(err)
						})
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		getmobile(){
			this.$http.get('/api/person/info/', {
				userId: this.userId
			}).then(res => {
				if (!res.mobile) {
					setTimeout(() => {
						uni.showToast({
							title: '请先绑定手机号再再查看详情',
							icon: 'none'
						});
					}, 100);
					uni.navigateTo({
						url: '../my/sms'
					})
				}
			}).catch((e) => {});
		}
	}
}
const puImage = async (e, _this) => {
	let cacheImg = [];
	for (let i = 0, len = _this.card.images.length; i < len; i++) {
		cacheImg.push(_this.card.images[i]);
	}
	uni.previewImage({
		current: cacheImg[e.currentTarget.dataset.idx],
		urls: cacheImg
	});
};
